<template>
    <yd-layout>
        <yd-navbar title="请假汇总" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="selectDepart">
            技术部 - 办公信息化室
        </div>
        <div class="topTitle">
            请假汇总数据
        </div>
        <div class="top">
            <yd-flexbox>
                <div class="nameColumn">姓名</div>
                <yd-flexbox-item>申请时间</yd-flexbox-item>
                <yd-flexbox-item>请假天数</yd-flexbox-item>
                <div class="operationColumn">操作</div>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid">
            <div class="fluidWidth numList" v-for="(item, index) in dataGroup" key="index">
                <yd-flexbox>
                    <div class="name" v-text="item.empname">王小明</div>
                    <div class="time">2017-06-12</div>
                    <yd-flexbox-item>12:34</yd-flexbox-item>
                    <yd-flexbox-item>3</yd-flexbox-item>
                    <div class="operation">详细</div>
                </yd-flexbox>
            </div>
            <div class="fluidWidth numList">
                <yd-flexbox>
                    <div class="name">王小明</div>
                    <div class="time">2017-06-12</div>
                    <yd-flexbox-item>12:34</yd-flexbox-item>
                    <yd-flexbox-item>3</yd-flexbox-item>
                    <div class="operation">详细</div>
                </yd-flexbox>
            </div>
        </div>
    </yd-layout>
</template>
<script>
export default {
    name: 'collect',
    data() {
        return {
            dataGroup: []
        }
    },
    mounted() {
        document.setTitle('智能报表/请假汇总');
        this.getTotalInfo();
    },
    methods: {
        getTotalInfo: function () {
            let vm = this;
            vm.post(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryDepHistoryRecord.action', {
                'type': 'askforleave',
                'depName': 'yewubu',
                'depID': '540',
                'mouth': '6',
                'queryType': 'on',
                'personID': ''
            }, function (res) {
                vm.dataGroup = res.jsonData;
                console.log(res);
            })
        }
    }
}
</script>
<style scoped>
.selectDepart {
    height: 0.68rem;
    width: 100%;
    background-color: #fff;
    text-align: center;
    line-height: 0.68rem;
    font-size: 0.26rem;
    margin-bottom: 0.09rem;
}

.selectDepart:after {
    content: '';
    display: inline-block;
    height: 0.4rem;
    width: 0.6rem;
    background: url(../../../static/images/reportForms/2.png) no-repeat;
    background-position: 0.15rem 0.24rem;
    background-size: 0.26rem;
}

.topTitle {
    height: 0.87rem;
    width: 100%;
    background: #fff;
    line-height: 0.87rem;
    font-size: 0.3rem;
    padding-left: 0.28rem;
}

.top {
    background-color: #ffc774;
    height: 0.57rem;
    padding: 0 0.33rem;
    color: #fff;
    line-height: 0.57rem;
    font-size: 0.26rem;
    font-weight: bold;
}

.nameColumn {
    width: 2.3rem;
}

.operationColumn {
    width: 0.7rem;
}

.name {
    width: 1.64rem;
}

.time {
    padding-right: 0.22rem;
}

.mainContentFluid {
    margin-top: 0;
}

.numList {
    padding: 0 0;
    margin: 0 0.33rem;
    padding-top: .27rem;
    padding-bottom: .27rem;
    font-size: 0.28rem;
    border-bottom: 1px solid #e5e5e5;
}

.operation {
    width: 1.07rem;
    text-align: center;
    background-color: #5cb0f4;
    color: #fff;
    border-radius: 0.05rem;
    height: 0.47rem;
    line-height: 0.47rem;
}
</style>


